import { Injectable } from '@angular/core';
import { Employee } from './employee';
//import { EMP } from './mock-employee';

import { Observable, of } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { catchError, tap, map } from 'rxjs/operators';

const httpOptions = {
    headers: new HttpHeaders({'Content-Type':'application/json'})
}

@Injectable({
  providedIn: 'root'
})
export class EmployeeService {
  
  private empUrl = 'api/employees';
    
  getEmployees(): Observable<Employee[]> {
      return this.http.get<Employee[]>(this.empUrl).pipe(
        catchError(this.handleError('getEmployees',[]))
      );
  }
     

  getEmployee(id: number): Observable<Employee>{
      const url = `${this.empUrl}/${id}`;
      return this.http.get<Employee>(url).pipe(
        catchError(this.handleError<Employee>(`getEmployee id=${id}`))
      );
  }    

    
  private handleError<T> (operation='operation', result?: T){
      return (error: any): Observable<T> => {
              console.error(error);
              return of(result as T);
              };
  }
    
    
  updateEmployee(employee: Employee): Observable<any> {
      return this.http.put(this.empUrl, employee, httpOptions).pipe(
        catchError(this.handleError<any>('updateHero'))
      );
  }
    
  addEmployee(employee: Employee): Observable<Employee> {
      return this.http.post<Employee>(this.empUrl, employee, httpOptions).pipe(
        catchError(this.handleError<Employee>('addEmployee'))
      );
  }
    
    deleteEmployee(employee: Employee|number): Observable<Employee> {
        const id = typeof employee === 'number' ? employee : employee.id;
        const url = `${this.empUrl}/${id}`;
        
        return this.http.delete<Employee>(url, httpOptions).pipe(
            catchError(this.handleError<Employee>('deleteHero'))
        );
    }
    
    searchEmployees(term: string): Observable<Employee[]>{
        if(!term.trim()){
            return of([]);
        }
        return this.http.get<Employee[]>(`${this.empUrl}/?name=${term}`).pipe(
            catchError(this.handleError<Employee[]>('searchHeroes', []))
        );
    }
    
  constructor(
  private http: HttpClient) { }
}
