import { Component, OnInit } from '@angular/core';
import { Employee } from '../employee';
//import { EMP } from '../mock-employee';

import { EmployeeService } from '../employee.service';
//import { AddNewComponent } from '../add-new/add-new.component';

@Component({
  selector: 'app-data-display',
  templateUrl: './data-display.component.html',
  styleUrls: ['./data-display.component.css']
})
export class DataDisplayComponent implements OnInit {

//new below
  counter = false;
  temp = new Employee();
    
  switchIt(){
         this.counter= (this.counter==true) ? false:true ;
  }    
    
  add(){
      this.temp.activated = true;
      alert("New employee "+this.temp.name+" added!");
      this.employeeService.addEmployee(this.temp).subscribe(employee => {this.employees.push(employee)});
      this.switchIt();
      this.temp = new Employee();
  }
    
//new above    
    
  employees: Employee[] ;    
  employeeHead = ['ID', 'NAME', 'Starting Date', 'Email', 'Description', 'Edit', 'Activate/Deactivate', 'Delete']; toggleFlag: boolean = false;
    
  getEmployees(): void {
      this.employeeService.getEmployees().subscribe(employees => this.employees = employees);
  }
    
  delete(employee: Employee): void{
      this.employees = this.employees.filter(h => h!=employee);
      this.employeeService.deleteEmployee(employee).subscribe();
  }
    
  deactivate(employee: Employee): void{
      employee.activated = false;
  }
   
  activate(employee: Employee): void{
      employee.activated = true;
  }
    
  confirmDelete(employee: Employee){
      if(confirm("Are you sure to delete the "+employee.name+" data?")){
          this.delete(employee);
      }
  }
  
  confirmCancel() {
      if(confirm("Are you sure you want to cancel.\nAll the data (IF) entered will be lost.")){   
          this.switchIt();
         }
  }
    
  constructor(private employeeService: EmployeeService) { }

  ngOnInit() {
      this.getEmployees();
  }

}
