import { Directive } from '@angular/core';
import { NG_VALIDATORS, Validator, AbstractControl } from '@angular/forms';

@Directive({
  selector: '[appEmailValidation]',
  providers: [{provide: NG_VALIDATORS, useExisting: EmailValidationDirective, multi: true}]
})
export class EmailValidationDirective implements Validator {

    
    
    validate(control: AbstractControl){    
    const elementValue = control.value;
    const reg = new RegExp('^[a-zA-Z0-9.=-]+@[a-z]+\.[a-z]{2,3}$');
    
    if(elementValue === null || elementValue === undefined || elementValue === '' ){
        return {'cus_required': 'Email ID is required'};
    }        
    else if(!reg.test(elementValue)){
        return {'cus_pattern': 'Enter a valid email address'};
    }

    return null;
}
  constructor() { }

}
