import { Directive } from '@angular/core';
import { NG_VALIDATORS, Validator, AbstractControl } from '@angular/forms';

@Directive({
  selector: '[appNameValidation]',
  providers: [{provide: NG_VALIDATORS, useExisting: NameValidationDirective, multi: true}]
})
export class NameValidationDirective implements Validator {

    
validate(control: AbstractControl){    
    const elementValue = control.value;
    const reg = new RegExp('[a-zA-Z]+\\.?');
    
    if(elementValue === null || elementValue === undefined || elementValue === '' ){
        return {'cus_required': 'Name is required'};
    }        
    else if(!reg.test(elementValue)){
        return {'cus_pattern': 'Enter a valid name'};
    }

    return null;
}
    
    
  constructor() { }

}
